.container {
  display: flex;
  flex-direction: column;
  height: 300px;
  width: auto;
}
.content {
  display: flex;
  flex-direction: column;
  border: 1px solid white;
  background-color: #ffffff;
  height: 50%;
  border-radius: 1%;
  text-align: center;
  justify-content: center;
}
